<template>
  <div class="form-container">
    <el-form :model="form" label-width="120px" class="form">
      <el-row :gutter="10">
        <el-col :span="12">
          <el-form-item label="表名" class="form-item">
            <el-input v-model="form.tableName"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="生成sql数据数量" class="form-item">
            <el-input v-model.number="form.dataNum"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-form-item label="表字段" class="form-item">
            <el-input type="textarea" v-model="form.tableFields"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="10">
        <el-col :span="12">
          <el-form-item label="模块名" class="form-item">
            <el-input v-model="form.moduleName"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="前端url名称" class="form-item">
            <el-input v-model="form.url"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-button
            type="primary"
            @click="handleSubmit()"
            class="form-item submit-btn mgc"
            >一键生成
          </el-button>
        </el-col>
      </el-row>
    </el-form>
    <div class="container" v-if="showTemplate">
      <el-row>
        <el-col :span="24">
          <p>对应 java 语句：</p>
          <pre
            class="preCode"
            v-highlight
          ><code class="sql">{{ singleJavaCode }}</code><el-button v-clipboard="singleJavaCode"
                                                                   type="primary"
                                                                   class="copy-btn">复制</el-button></pre>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <p>生成随机的{{ form.dataNum }}条 sql 语句：</p>
          <pre
            class="preCode"
            v-highlight
          ><code class="sql">{{ randomSqlCode }}</code><el-button v-clipboard="randomSqlCode" type="primary"
                                                                  class="copy-btn">复制</el-button></pre>
        </el-col>
      </el-row>
      <el-row :gutter="10">
        <el-col :span="12">
          <div>
            <p>对应 pgsql 语句：</p>
            <pre
              class="preCode"
              v-highlight
            ><code class="sql">{{ pgsqlCode }}</code><el-button v-clipboard="pgsqlCode" type="primary"
                                                                class="copy-btn">复制</el-button></pre>
          </div>
        </el-col>
        <el-col :span="12">
          <div>
            <p>对应 mysql 语句：</p>
            <pre
              class="preCode"
              v-highlight
            ><code class="sql">{{ mysqlCode }}</code><el-button v-clipboard="mysqlCode" type="primary"
                                                                class="copy-btn">复制</el-button></pre>
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="10">
        <el-col :span="12">
          <div>
            <p>对应的 vo 文件代码：{{ moduleName }}VO</p>
            <pre
              class="preCode"
              v-highlight
            ><code class="java">{{ voJavaCode }}</code><el-button v-clipboard="voJavaCode" type="primary"
                                                                  class="copy-btn">复制</el-button></pre>
          </div>
        </el-col>
        <el-col :span="12">
          <div>
            <p>对应的 condition 文件代码：{{ moduleName }}PageCondition</p>
            <pre
              class="preCode"
              v-highlight
            ><code class="java">{{ pageConditionJavaCode }}</code><el-button v-clipboard="pageConditionJavaCode"
                                                                             type="primary"
                                                                             class="copy-btn">复制</el-button></pre>
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="10">
        <el-col :span="12">
          <div>
            <p>对应的 repository/entity 文件代码：{{ moduleName }}</p>
            <pre
              class="preCode"
              v-highlight
            ><code class="java">{{ entityCode }}</code><el-button v-clipboard="entityCode" type="primary"
                                                                  class="copy-btn">复制</el-button></pre>
          </div>
        </el-col>
        <el-col :span="12">
          <div>
            <p>对应的 repository/mapper 文件代码：{{ moduleName }}Mapper</p>
            <pre
              class="preCode"
              v-highlight
            ><code class="java">{{ mapperCode }}</code><el-button v-clipboard="mapperCode" type="primary"
                                                                  class="copy-btn">复制</el-button></pre>
          </div>
        </el-col>
      </el-row>

      <el-row :gutter="10">
        <el-col :span="12">
          <div>
            <p>对应的 repository 文件代码：{{ moduleName }}Repository</p>
            <pre
              class="preCode"
              v-highlight
            ><code class="java">{{ repositoryCode }}</code><el-button v-clipboard="repositoryCode" type="primary"
                                                                      class="copy-btn">复制</el-button></pre>
          </div>
        </el-col>
        <el-col :span="12">
          <div>
            <p>对应的 service/impl 文件代码：{{ moduleName }}Impl</p>
            <pre
              class="preCode"
              v-highlight
            ><code class="java">{{ implCode }}</code><el-button v-clipboard="implCode" type="primary"
                                                                class="copy-btn">复制</el-button></pre>
          </div>
        </el-col>
      </el-row>

      <el-row :gutter="10">
        <el-col :span="12">
          <div>
            <p>对应的 service 文件代码：I{{ moduleName }}Service</p>
            <pre
              class="preCode"
              v-highlight
            ><code class="java">{{ serviceCode }}</code><el-button v-clipboard="serviceCode" type="primary"
                                                                   class="copy-btn">复制</el-button></pre>
          </div>
        </el-col>
        <el-col :span="12">
          <div>
            <p>对应的controller文件代码：</p>
            <pre
              class="preCode"
              v-highlight
            ><code class="java">{{ controllerJavaCode }}</code><el-button v-clipboard="controllerJavaCode"
                                                                          type="primary"
                                                                          class="copy-btn">复制</el-button></pre>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import beautify from "js-beautify";

// 根据语言类型设置格式化选项
const OPTIONS = {
  indent_size: 4,
  indent_char: " ",
  preserve_newlines: true,
  max_preserve_newlines: 2,
};

// 其中indent_size表示缩进的空格数，indent_char表示缩进使用的字符，preserve_newlines表示是否保留原有的换行符，max_preserve_newlines表示最多保留的换行符数量。你可以根据具体的需求进行调整。

// 待格式化的代码字符串
// const codeString =
//   'public static void main(String[] args) {\n    System.out.println("Hello, World!");\n}';

// 格式化代码
// const formattedCode = beautify(codeString, options);

// console.log(formattedCode);

export default {
  name: "GenerateJavaTemplate",
  data() {
    return {
      form: {
        tableName: "",
        tableFields: "",
        permission: "",
        dataNum: 0,
        moduleName: "",
        url: "",
      },
      showTemplate: false,
      singleJavaCode: "",
      pgsqlCode: "",
      moduleName: "",
      controllerJavaCode: "",
      voJavaCode: "",
      entityCode: "",
      pageConditionJavaCode: "",
      mapperCode: "",
      repositoryCode: "",
      implCode: "",
      serviceCode: "",
      mysqlCode: "",
      randomSqlCode: "",
      userInputCode: "",
    };
  },
  methods: {
    handleSubmit() {
      this.showTemplate = false;
      this.moduleName = this.capitalizeFirstLetter(this.form.moduleName);
      this.randomSqlCode = this.generateSqlCode(
        this.form.tableFields,
        this.form.dataNum
      );
      this.singleJavaCode = this.generateSingleCode();
      this.pgsqlCode = this.generatePgSql();
      this.mysqlCode = this.generateMysqlCode();
      this.controllerJavaCode = this.generateController();
      this.voJavaCode = this.generateVoCode();
      this.pageConditionJavaCode = this.generatePageConditionCode();
      this.entityCode = this.generateEntityCode();
      this.mapperCode = this.generateMapperCode();
      this.repositoryCode = this.generateRepositoryCode();
      this.implCode = this.generateImplCode();
      this.serviceCode = this.generateServiceCode();
      // console.log(this.controllerJavaCode, "this.controllerJavaCode ");
      this.$nextTick(() => {
        let timer = null;
        timer = setTimeout(() => {
          clearTimeout(timer);
          this.showTemplate = true;
        }, 500);
      });
      // this.showTemplate = true;

      // console.log(this.pgsqlCode + "pgs");
    },
    generateSqlCode(field, count) {
      const lines = field.split(",\n").filter((line) => line.trim() !== ""); // 按行切割，并过滤掉空行
      const fields = lines.map((line) => {
        const handleData = this.removeExtraSpaces(line);
        const parts = handleData.split(/\s+/); // 按空格或制表符切割
        const name = parts[0];
        const type = parts[1];
        const notNull = parts.includes("NOT") && parts.includes("NULL"); // 判断是否不允许为空
        // console.log(name, type, notNull, "ss");
        return { name, type, notNull };
      });
      let sql = "";
      for (let i = 0; i < count; i++) {
        sql += `INSERT INTO ${this.form.tableName} (${fields
          .map((field) => field.name)
          .join(", ")})
                VALUES (`;
        sql += fields
          .map((field) => {
            if (
              field.type.toUpperCase().startsWith("VARCHAR") ||
              field.type.toUpperCase().startsWith("CHAR") ||
              field.type.toUpperCase().startsWith("TEXT")
            ) {
              return `'${this.getRandomString()}'`;
            } else if (
              field.type.toUpperCase().startsWith("INT") ||
              field.type.toUpperCase().startsWith("BIGINT") ||
              field.type.toUpperCase().startsWith("DECIMAL") ||
              field.type.toUpperCase().startsWith("SMALLINT")
            ) {
              return Math.floor(Math.random() * 1000);
            } else if (
              field.type.toUpperCase().startsWith("FLOAT") ||
              field.type.toUpperCase().startsWith("DOUBLE")
            ) {
              return Math.random() * 1000;
            } else if (
              field.type.toUpperCase() === "DATE" ||
              field.type.toUpperCase() === "TIME" ||
              field.type.toUpperCase() === "DATETIME" ||
              field.type.toUpperCase() === "TIMESTAMP"
            ) {
              return `'${this.getRandomDateTime()}'`;
            } else {
              return "null";
            }
          })
          .join(", ");
        sql += ");\n";
      }
      return sql;
    },
    getRandomString() {
      return (
        Math.random().toString(36).substring(2, 15) +
        Math.random().toString(36).substring(2, 15)
      );
    },
    getRandomDateTime() {
      const date = new Date(
        Date.now() - Math.floor(Math.random() * 10000000000)
      );
      return `${date.getFullYear()}-${this.padZero(
        date.getMonth() + 1
      )}-${this.padZero(date.getDate())} ${this.padZero(
        date.getHours()
      )}:${this.padZero(date.getMinutes())}:${this.padZero(date.getSeconds())}`;
    },
    padZero(num) {
      return num < 10 ? `0${num}` : num;
    },
    /**
     * @author: Robyn
     * @date: 2023/4/1 16:06
     * @Description: 将字符串的换行符改成空格
     */
    formattedCode(str) {
      return str.replace(/\n/g, " ");
    },
    /**
     * @author: Robyn
     * @date: 2023/4/1 15:44
     * @Description: 去除多空格，保留一个空格，并且去除尾缀逗号
     */
    removeExtraSpaces(str) {
      if (str.endsWith(",")) {
        str = str.slice(0, -1);
      }
      return str.replace(/\s+/g, " ").trim();
    },
    fontmattedShowCode(str) {
      return this.removeExtraSpaces(str).replace(/,\s*/g, ",\n");
    },
    capitalizeFirstLetter(str) {
      return str.charAt(0).toUpperCase() + str.slice(1);
    },
    generateSingleCode() {
      return this.generateJavaCode(this.form.tableFields);
    },
    /**
     * @author: Robyn
     * @date: 2023/4/1 15:45
     * @Description: 生成 pgsql 相关语句
     */
    generatePgSql() {
      console.log(
        this.fontmattedShowCode(this.form.tableFields),
        "fontmattedShowCode"
      );
      let template = `CREATE TABLE ${this.form.tableName}
                      (
                        id BIGINT NOT NULL
                          CONSTRAINT ${this.form.tableName}_pkey
                            PRIMARY KEY,
                        ${this.fontmattedShowCode(this.form.tableFields)}
                      );`;
      return beautify(template, OPTIONS);
    },
    generateMysqlCode() {
      let template = `CREATE TABLE ${this.form.tableName}
                      (
                        id BIGINT NOT NULL
                          CONSTRAINT ${this.form.tableName}_pkey
                            PRIMARY KEY,
                        ${this.fontmattedShowCode(this.form.tableFields)}
                      );`;
      return beautify(template, OPTIONS);
    },
    convertDBTypeToJavaType(dbType) {
      const varcharRegex = /^VARCHAR\((\d+)\)$/;
      const decimalRegex = /^DECIMAL\((\d+),(\d+)\)$/;

      if (
        dbType.toUpperCase().startsWith("INT") ||
        dbType.toUpperCase().startsWith("SMALLINT")
      ) {
        return "Integer";
      } else if (dbType.toUpperCase().startsWith("BIGINT")) {
        return "Long";
      } else if (dbType.toUpperCase().startsWith("FLOAT")) {
        return "Float";
      } else if (dbType.toUpperCase().startsWith("DOUBLE")) {
        return "Double";
      } else if (dbType.toUpperCase().startsWith("DECIMAL")) {
        const [, precision, scale] = dbType.match(decimalRegex);
        return `BigDecimal(${precision}, ${scale})`;
      } else if (
        dbType.toUpperCase().startsWith("CHAR") ||
        dbType.toUpperCase().startsWith("VARCHAR") ||
        dbType.toUpperCase().startsWith("TEXT")
      ) {
        const [, length] = dbType.toUpperCase().match(varcharRegex);
        return `String`;
      } else if (
        dbType.toUpperCase().startsWith("DATE") ||
        dbType.toUpperCase().startsWith("TIME") ||
        dbType.toUpperCase().startsWith("DATETIME") ||
        dbType.toUpperCase().startsWith("TIMESTAMP")
      ) {
        return "LocalDateTime";
      } else if (dbType.toUpperCase().startsWith("BOOLEAN")) {
        return "Boolean";
      } else {
        return "Object";
      }
    },
    convertDBColumnToJavaProperty(dbColumn) {
      let [columnName, columnType, isNotNull] = dbColumn.split(" ");
      let javaType = this.convertDBTypeToJavaType(columnType);
      let propertyName = columnName.replace(/_(\w)/g, (match, p1) =>
        p1.toUpperCase()
      );

      return `private ${javaType} ${propertyName};\n`;
    },
    generateJavaCode(str) {
      console.log(str, "str");
      const strs = str.split(",\n");
      let finalStatement = "";
      strs.forEach((item) => {
        const handleItem = this.removeExtraSpaces(item);
        console.log(handleItem, "handleItem");
        finalStatement += handleItem
          ? this.convertDBColumnToJavaProperty(handleItem)
          : "";
      });
      return finalStatement;
    },
    generateCreateFunction(javaCode, moduleName, condition) {
      let varsStatement = javaCode.split(";\n").join(";");
      const varRegex = /private\s+(\w+)\s+(\w+);/g;
      let match = varRegex.exec(varsStatement);
      let vars = [];
      while (match !== null) {
        vars.push({ name: match[2], type: match[1] });
        match = varRegex.exec(varsStatement);
      }
      let classCode = "";
      for (let i = 0; i < vars.length; i++) {
        let { name } = vars[i];
        classCode += `${moduleName}.set${
          name.charAt(0).toUpperCase() + name.slice(1)
        }(${condition}.get${
          name.charAt(0).toUpperCase() + name.slice(1)
        }());\n`;
      }
      return classCode;
    },
    generateController() {
      let template = `
@Slf4j
@RestController
@Tag(name = "${this.moduleName}模块")
@RequiredArgsConstructor
@RequestMapping("")
public class ${this.moduleName}Controller implements BaseController, BaseDownloadController {
  private final I${this.moduleName}Service service;
  @GetMapping("${this.form.url}")
  @Operation(summary = "查询${this.moduleName}分页数据")
  @PamPrivilege(value = PASS)
  public ResponseVO<PageVO<${this.moduleName}ListVO>> findPage(@Valid ${this.moduleName}PageCondition condition) {
    return ResponseVO.success(service.findPage(condition));
  }
}
      `;
      return beautify(template, OPTIONS);
    },
    generateVoCode() {
      const javaCode = this.generateJavaCode(this.form.tableFields);
      const createFun = this.generateCreateFunction(
        javaCode,
        "vo",
        "condition"
      );
      let template = `
@Data
@NoArgsConstructor
@AllArgsConstructor
@Schema(description = "${this.moduleName}列表")
public class ${this.moduleName}ListVO implements BaseVO {
    ${javaCode}
    public static ${this.moduleName}ListVO from(${this.moduleName} condition) {
        ${this.moduleName}ListVO vo = new ${this.moduleName}ListVO();
        ${createFun}
        return vo;
    }
}
      `;
      return beautify(template, OPTIONS);
    },
    generatePageConditionCode() {
      let template = `
@Data
@NoArgsConstructor
@AllArgsConstructor
@EqualsAndHashCode(callSuper = true)
public class ${this.moduleName}PageCondition extends AbstractPageCondition<${this.moduleName}> implements ConditionJwtAware {
    @JsonIgnore
    @Schema(hidden = true)
    private JwtBody jwtBody;

    @JsonIgnore
    @Schema(hidden = true)
    private LanguageEnum language;
    @JsonIgnore
    @Schema(hidden = true, description = "后端设置，数据权限")
    private List<Long> canAccessDepartmentIds;

    @Override
    public OrderItem getDefaultOrder() {
        return OrderItem.desc("created_time");
    }
}
      `;
      return beautify(template, OPTIONS);
    },
    generateEntityCode() {
      const javaCode = this.generateJavaCode(this.form.tableFields);
      const createFun = this.generateCreateFunction(
        javaCode,
        this.form.moduleName,
        "condition"
      );
      let template = `
@Data
@Slf4j
@TableName(value = "${this.form.tableName}", autoResultMap = true)
public class ${this.moduleName} implements BaseEntity<${this.moduleName}> {
    @TableId(type = IdType.AUTO)
    private Long id;
     ${javaCode}

    public static ${this.moduleName} create(long id, ${this.moduleName}Condition condition) {
        ${this.moduleName} ${this.form.moduleName} = new ${this.moduleName}();
        LocalDateTime now = LocalDateTime.now();
        ${this.form.moduleName}.setId(id);
        ${createFun}
        resourceSession.setUpdateTime(now);
        return resourceSession;
    }
}

      `;
      return beautify(template, OPTIONS);
    },
    generateMapperCode() {
      let template = `
      public interface ${this.moduleName}Mapper extends BaseMapper<${this.moduleName}> {

}
      `;
      return beautify(template, OPTIONS);
    },
    generateRepositoryCode() {
      let template = `
@Repository
public class ${this.moduleName}Repository extends ServiceImpl<${this.moduleName}Mapper, ${this.moduleName}> implements BaseIdGenerator {
  public IPage<${this.moduleName}> findPage(long id, ${this.moduleName}PageCondition condition) {
    return baseMapper.selectPage(condition.getPage(), Wrappers.lambdaQuery(${this.moduleName}.class)
        .eq(${this.moduleName}::getId, id)
        );
  }
}
      `;
      return beautify(template, OPTIONS);
    },
    generateImplCode() {
      let template = `
@Slf4j
@Service
@RequiredArgsConstructor
public class ${this.moduleName}ServiceImpl implements I${this.moduleName}Service {
    private final ${this.moduleName}Repository repository;
    private final DepartmentFeignClient departmentApi;

    @Override
    public PageVO<${this.moduleName}ListVO> findPage(${this.moduleName}PageCondition condition) {
        JwtBody jwtBody = condition.getJwtBody();
        List<Long> canAccessDepartmentIds = departmentApi.getIdsWithPrivilegeConfig(jwtBody.getUserId()).getData();
        if (CollectionUtils.isEmpty(canAccessDepartmentIds)) {
            return PageVO.empty();
        }
        condition.setCanAccessDepartmentIds(canAccessDepartmentIds);
        IPage<${this.moduleName}> page = repository.findPage(condition);
        List<${this.moduleName}> records = page.getRecords();
        if (records.isEmpty()) {
            return PageVO.empty();
        }


        List<${this.moduleName}ListVO> vos = records.stream()
                .map(${this.moduleName}ListVO::from)
                .collect(Collectors.toList());

        return PageVO.create(page, vos);
    }
}
      `;
      return beautify(template, OPTIONS);
    },
    generateServiceCode() {
      let template = `
public interface I${this.moduleName}Service {
  PageVO<${this.moduleName}ListVO> findPage(${this.moduleName}PageCondition condition);
}      
      `;
      return beautify(template, OPTIONS);
    },
  },
};
</script>

<style scoped>
